@use 'base/variables' as v;

/*================================================
Footer CSS
=================================================*/

.footer-area {
	background-color: v.$bgnegro;
	padding-top: 100px;
}
.single-footer-widget {
	h3 {
		margin-bottom: 25px;
		color: v.$blanco;
		font-size: 20px;
		font-weight: 600;
	}
	.contact-us-link {
		margin-bottom: 0;
		list-style-type: none;
		padding-left: 0;
		padding-right: 10px;
		li {
			margin-bottom: 12px;
			color: v.$grisClaro;
			position: relative;
			padding-left: 25px;
			i {
				color: v.$blanco;
				font-size: 18px;
				position: absolute;
				left: 0;
				top: 1px;
			}
			a {
				display: inline-block;
				color: v.$grisClaro;
				&:hover {
					color: v.$rojo;
				}
				&:focus {
					color: v.$rojo;
				}
			}
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	.support-link {
		padding-left: 0;
		list-style-type: none;
		margin-bottom: 0;
		li {
			margin-bottom: 11px;
			&:last-child {
				margin-bottom: 0;
			}
			a {
				display: inline-block;
				color: v.$grisClaro;
				&:hover {
					color: v.$rojo;
					padding-left: 7px;
				}
				&:focus {
					color: v.$rojo;
					padding-left: 7px;
				}
			}
		}
	}
	.useful-link {
		padding-left: 0;
		list-style-type: none;
		margin-bottom: 0;
		li {
			margin-bottom: 11px;
			&:last-child {
				margin-bottom: 0;
			}
			a {
				display: inline-block;
				color: v.$grisClaro;
				&:hover {
					color: v.$rojo;
					padding-left: 7px;
				}
				&:focus {
					color: v.$rojo;
					padding-left: 7px;
				}
			}
		}
	}
	.social-link {
		padding-left: 0;
		list-style-type: none;
		margin-top: 25px;
		margin-bottom: 0;
		li {
			display: inline-block;
			margin-right: 2px;
			a {
				width: 30px;
				height: 30px;
				text-align: center;
				color: v.$rojo;
				background-color: v.$blanco;
				font-size: 18px;
				position: relative;
				border-radius: 2px;
				i {
					position: absolute;
					left: 0;
					top: 50%;
					right: 0;
					-webkit-transform: translateY(-49%);
					transform: translateY(-49%);
				}
				&:hover {
					background-color: v.$rojo;
					color: v.$blanco;
				}
				&:focus {
					background-color: v.$rojo;
					color: v.$blanco;
				}
			}
		}
	}
	.newsletter-box {
		p {
			color: v.$grisClaro;
			font-family:"Poppins", sans-serif;
		}
		form {
			position: relative;
			label {
				display: block;
				margin-bottom: 10px;
				color: v.$blanco;
				font-size: 15px;
			}
			.input-newsletter {
				width: 100%;
				background-color: transparent;
				border: 1px solid #848282;
				border-radius: 5px;
				height: 43px;
				padding-left: 10px;
				color: v.$blanco;
				font-size: 14px;
				-webkit-transition: 0.5s;
				transition: 0.5s;
				&::-webkit-input-placeholder {
					color: v.$grisClaro;
				}
				&:-ms-input-placeholder {
					color: v.$grisClaro;
				}
				&::-ms-input-placeholder {
					color: v.$grisClaro;
				}
				&::placeholder {
					color: v.$grisClaro;
				}
				&:focus {
					padding-left: 12px;
					border-color: v.$rojo;
				}
			}
			button {
				display: block;
				margin-top: 0.9rem;
				width: 100%;
				border-radius: 0.25rem;
				background-color: v.$rojo;
				color: v.$blanco;
				border: none;
				padding: 12px 25px;
				-webkit-transition: 0.5s;
				transition: 0.5s;
				font-weight: 500;
				font-size: 15px;
				&:hover {
					background-color: v.$azul;
					color: v.$blanco;
				}
				&:focus {
					background-color: v.$azul;
					color: v.$blanco;
				}
			}
			#validator-newsletter {
				position: absolute;
				left: 0;
				bottom: -35px;
				color: v.$blanco;
			}
		}
	}
}
.footer-bottom-area {
	margin-top: 70px;
	text-align: center;
	background-color: #090a0e;
	padding-top: 30px;
	padding-bottom: 30px;
	.logo {
		margin-bottom: 18px;
	}
	p {
		color: v.$grisClaro;
		font-size: 14px;
		i {
			position: relative;
			top: 1px;
		}
		a {
			display: inline-block;
			font-weight: 600;
			color: v.$blanco;
			&:hover {
				color: v.$rojo;
				text-decoration: underline;
			}
			&:focus {
				color: v.$rojo;
				text-decoration: underline;
			}
		}
	}
}