@use 'base/variables' as v;


/*================================================
Header CSS
=================================================*/

.header-area {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    height: auto;
    width: 100%;
    z-index: 999;

    .header-area.p-relative {
        position: relative;
      }
  }

.top-header {
    background-color: #d42323;
    padding-top: 15px;
    padding-bottom: 25px;
}

.top-header-contact-info {
    padding-left: 0;
    margin-bottom: 0;
    list-style-type: none;

    li {
        /* display: inline-block; */
        color: #ffffff;
        margin-right: 20px;
        position: relative;
        padding-left: 38px;
        font-size: 13px;

        i {
            position: absolute;
            left: -30px;
            top: 50%;
            -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
            font-size: 24px;
          }
      }
      span {
        display: block;
        margin-bottom: 2px;
      }
      a {
        display: inline-block;
        color: #ffffff;
        font-size: 15px;
        font-weight: 700;

        &:hover {
            opacity: 0.95;
        }
        &:focus {
            opacity: 0.95;
        }
      }

      &:last-child {
        margin-right: 0;
    }
  }

  .top-header-contact-info {
	li {
		a {
			&:hover {
				opacity: 0.95;
			}
			&:focus {
				opacity: 0.95;
			}
		}
		&:last-child {
			margin-right: 0;
		}
	}
}

.top-header-adela {
    background-color: #d42323;
    padding-top: 0;
    padding-bottom: 0;

    .social-block {
        display: flex;
        justify-content: space-between;
      }

    .top-header-contact-info {
        /* display: inline-block; */
        padding-top: 10px;
        padding-bottom: 10px;

        .phone {
            display: flex;
            flex-direction: row;
          }

        li {
            padding-left: 0;
            font-size: 14px;
            color: #ffffff;

            a {
                font-size: 14px;
                color: #ffffff;
                font-weight: 500;
              }
          }
      }  
      
      .top-header-social {
        border-left: 1px solid rgba(255, 255, 255, 0.6);

        span {
            color: #ffffff;
          }

          a {
            color: #ffffff;
          }

      }

  }

  .top-header-social {
    display: inline-block;
    margin-left: 20px;
    border-left: 1px solid #4a4646;
    /* padding-top: 10px;
    padding-bottom: 10px; */
    padding-left: 20px;


    span {
        display: inline-block;
        color: #cccccc;
        margin-right: 5px;
      }

    a {
        display: inline-block;
        color: #cccccc;
        position: relative;
        top: 2px;
        font-size: 17px;
        margin-right: 2px;


      }
  }


  .top-header-login-register {
    text-align: right;
    padding-left: 0;
    margin-bottom: 0;
    list-style-type: none;

    li {
        display: inline-block;
        color: #cccccc;
        margin-right: 20px;

        a{
            display: inline-block;
            color: v.$blanco;
            position: relative;
            padding-left: 20px;

            i {
                position: absolute;
                left: 0;
                top: 3px;
              }
          }
          &:last-child{
            margin-right: 0;
          }
      }
  }



  .navbar-area.header-sticky {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;
    -webkit-box-shadow: 0 2px 28px 0 rgba(0, 0, 0, 0.06);
    box-shadow: 0 2px 28px 0 rgba(0, 0, 0, 0.06);
    background: #ffffff !important;
    opacity: 0;
    visibility: hidden;
    -webkit-transition: 0.5s;
    transition: 0.5s;


    .raque-nav {
      margin-top: 0;

      .navbar {
        -webkit-box-shadow: unset;
        box-shadow: unset;
        padding: 0;

        .navbar-brand {
            padding-top: 0;
            padding-bottom: 0;
            padding-left: 0;
            padding-right: 0;
        }
      }



    }
  }
  
  .navbar-area.header-sticky.is-sticky {
    opacity: 1;
    visibility: visible;
  }
  
  .navbar-area.header-sticky.scrollUp {
    -webkit-transform: translateY(-85px);
    transform: translateY(-85px);
    opacity: 0;
    visibility: hidden;
  }


  .navbar-style-three {

    .raque-nav {
        margin-top: 0;
        background-color: #ffffff;

        .navbar {
            border-radius: 0;
            padding: 0;

            .navbar-nav {
                margin-left: auto;
                margin-right: 0;

                .nav-item {
                    position: relative;
                    padding: 0;
                }
              }

        .others-option {
                margin-left: 25px;
        }
          }

      }

  }
  
  .navbar-style-three.header-sticky .raque-nav .navbar .others-option {
    margin-left: 25px;
  }
  


  //Menu navbar responsivo

  .raque-responsive-nav {
    display: none;
}



