@use 'base/variables' as v;



/*================================================
Login CSS
=================================================*/
 .login-image img {
  display: none;
} 
.login-image {
	height: 100%;
	width: 100%;
	background-image: url(/build/img/slideshow/Preescolar-adela.webp);
	background-position: center center;
	background-size: cover;
	background-repeat: no-repeat;
}
.login-content {
	height: 100vh;
	.login-form {
		text-align: center;
		max-width: 500px;
		margin-left: auto;
		margin-right: auto;
		.logo {
			margin-bottom: 30px;
			a {
				display: inline-block;
			}
		}
		h3 {
			margin-bottom: 0;
			font-size: 35px;
			font-weight: 700;
		}
		p {
			margin-top: 8px;
			margin-bottom: 0;
			a {
				display: inline-block;
			}
		}
		form {
			margin-top: 35px;
			.form-group {
				margin-bottom: 15px;
			}
			.form-control {
				background-color: v.$blanco;
				color: v.$negro;
				border: none;
				-webkit-transition: 0.5s;
				transition: 0.5s;
				-webkit-box-shadow: 0px 5px 28.5px 1.5px rgba(149, 152, 200, 0.2) !important;
				box-shadow: 0px 5px 28.5px 1.5px rgba(149, 152, 200, 0.2) !important;
				height: 55px;
				font-size: 15px;
				&::-webkit-input-placeholder {
					-webkit-transition: 0.5s;
					transition: 0.5s;
				}
				&:-ms-input-placeholder {
					-webkit-transition: 0.5s;
					transition: 0.5s;
				}
				&::-ms-input-placeholder {
					-webkit-transition: 0.5s;
					transition: 0.5s;
				}
				&::placeholder {
					-webkit-transition: 0.5s;
					transition: 0.5s;
				}
				&:focus {
					&::-webkit-input-placeholder {
						color: transparent;
					}
					&:-ms-input-placeholder {
						color: transparent;
					}
					&::-ms-input-placeholder {
						color: transparent;
					}
					&::placeholder {
						color: transparent;
					}
				}
			}
			button {
				-webkit-transition: 0.5s;
				transition: 0.5s;
				display: block;
				width: 100%;
				padding: 13px 25px 12px 25px;
				position: relative;
				background-color: v.$rojo;
				color: v.$blanco;
				-webkit-box-shadow: 0px 5px 28.5px 1.5px rgba(255, 97, 47, 0.2);
				box-shadow: 0px 5px 28.5px 1.5px rgba(255, 97, 47, 0.2);
				border-width: 2px;
				border-style: solid;
				border-color: v.$rojo;
				border-radius: 5px;
				font-size: 14.5px;
				font-weight: 700;
				&:hover {
					background-color: v.$azul;
					color: v.$blanco;
					border-color: v.$azul;
				}
				&:focus {
					background-color: v.$azul;
					color: v.$blanco;
					border-color: v.$azul;
				}
			}
			.forgot-password {
				text-align: right;
				margin-top: 20px;
				a {
					display: inline-block;
					color: v.$rojo;
					text-decoration: underline;
					&:hover {
						color: v.$azul;
					}
				}
			}
			.connect-with-social {
				margin-top: 20px;
				button {
					display: block;
					width: 100%;
					position: relative;
					border: 1px solid #d42323;
					background-color: transparent;
					-webkit-transition: 0.5s;
					transition: 0.5s;
					padding: 12px 30px;
					border-radius: 2px;
					color: v.$rojo;
					font-weight: 500;
					i {
						position: absolute;
						top: 50%;
						-webkit-transform: translateY(-50%);
						transform: translateY(-50%);
						left: 15px;
						font-size: 20px;
					}
					&:hover {
						background-color: v.$rojo;
						color: v.$blanco;
						border-color: v.$rojo;
					}
				}
				button.facebook {
					border-color: v.$azulOscuro;
					color: v.$azulOscuro;
					&:hover {
						background-color: v.$azulOscuro;
						color: v.$blanco;
						border-color: v.$azulOscuro;
					}
				}
				button.twitter {
					margin-top: 10px;
					border-color: v.$azulClaro;
					color: v.$azulClaro;
					&:hover {
						background-color: v.$azulClaro;
						color: v.$blanco;
						border-color: v.$azulClaro;
					}
				}
			}
		}
	}
}