  /*default-btn*/
  .default-btn {
    -webkit-transition: 0.5s;
    transition: 0.5s;
    display: inline-block;
    padding: 13px 25px 12px 55px;
    position: relative;
    background-color: #d42323;
    color: #ffffff;
    border-width: 2px;
    border-style: solid;
    border-color: #d42323;
    border-radius: 1px;
    font-size: 14.5px;
    font-weight: 700;
  }
  
  .default-btn .label,
  .default-btn .icon-arrow {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-perspective: 1000;
    perspective: 1000;
  }
  
  .default-btn .label {
    display: inline-block;
    -webkit-transition: -webkit-transform 0.5s cubic-bezier(0.86, 0, 0.07, 1);
    transition: -webkit-transform 0.5s cubic-bezier(0.86, 0, 0.07, 1);
    transition: transform 0.5s cubic-bezier(0.86, 0, 0.07, 1);
    transition: transform 0.5s cubic-bezier(0.86, 0, 0.07, 1),
      -webkit-transform 0.5s cubic-bezier(0.86, 0, 0.07, 1);
  }
  
  .default-btn .icon-arrow {
    color: #ffffff;
    top: 11px;
    -webkit-transition: opacity 0.4s cubic-bezier(0.86, 0, 0.07, 1),
      -webkit-transform 0.5s cubic-bezier(0.86, 0, 0.07, 1);
    transition: opacity 0.4s cubic-bezier(0.86, 0, 0.07, 1),
      -webkit-transform 0.5s cubic-bezier(0.86, 0, 0.07, 1);
    transition: transform 0.5s cubic-bezier(0.86, 0, 0.07, 1),
      opacity 0.4s cubic-bezier(0.86, 0, 0.07, 1);
    transition: transform 0.5s cubic-bezier(0.86, 0, 0.07, 1),
      opacity 0.4s cubic-bezier(0.86, 0, 0.07, 1),
      -webkit-transform 0.5s cubic-bezier(0.86, 0, 0.07, 1);
    font-size: 25px;
  }
  
  .default-btn .icon-arrow.before {
    left: 20px;
    position: absolute;
    -webkit-transform-origin: left center;
    transform-origin: left center;
  }
  
  .default-btn .icon-arrow.after {
    opacity: 0;
    position: absolute;
    right: 20px;
    -webkit-transform: translateX(75%) scaleX(0.1);
    transform: translateX(75%) scaleX(0.1);
    -webkit-transform-origin: right center;
    transform-origin: right center;
  }
  
  .default-btn:hover,
  .default-btn:focus {
    background-color: #0474bc;
    color: #ffffff;
    border-color: #0474bc;
  }
  
  .default-btn:hover .label,
  .default-btn:focus .label {
    -webkit-transform: translateX(-32px);
    transform: translateX(-32px);
  }
  
  .default-btn:hover .icon-arrow.before,
  .default-btn:focus .icon-arrow.before {
    opacity: 0;
    -webkit-transform: translateX(-75%) scaleX(0.1);
    transform: translateX(-75%) scaleX(0.1);
  }
  
  .default-btn:hover .icon-arrow.after,
  .default-btn:focus .icon-arrow.after {
    opacity: 1;
    -webkit-transform: translateX(0) scaleX(1);
    transform: translateX(0) scaleX(1);
  }
  
