@use 'base/variables' as v;

/*================================================
Contenido CSS
=================================================*/

$color_1: #d42323;
$color_2: #ffffff;
$color_3: #727695;
$color_4: #252525;
$background-color_1: #0474bc;
$background-color_2: #ffffff;
$background-color_3: #d42323;
$background-color_4: #eaeaf2;

@-webkit-keyframes rotateMe {
	from {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}
@keyframes rotateMe {
	from {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}
.about-area {
	position: relative;
	z-index: 1;
}
.about-image {
	position: relative;
	padding-right: 100px;
	padding-bottom: 50px;
	img {
		border-radius: 5px;
		&:nth-child(2) {
			position: absolute;
			right: 10px;
			bottom: 0;
			z-index: 1;
		}
	}
}
.about-content {
	padding-left: 15px;
	.sub-title {
		text-transform: uppercase;
		display: block;
		color: $color_1;
		font-weight: 500;
		margin-bottom: 10px;
	}
	h2 {
		margin-bottom: 15px;
		line-height: 1.4;
		font-size: 38px;
		font-weight: 700;
	}
	h6 {
		margin-bottom: 14px;
		line-height: 1.6;
	}
	.features-text {
		margin-top: 25px;
		h5 {
			margin-bottom: 22px;
			position: relative;
			font-size: 17px;
			font-weight: 600;
			padding-left: 47px;
			padding-top: 7px;
		}
		i {
			position: absolute;
			left: 0;
			top: 0;
			width: 35px;
			height: 35px;
			background-color: $background-color_1;
			text-align: center;
			line-height: 35px;
			color: $color_2;
			font-size: 20px;
			-webkit-box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
			box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
			border-radius: 50%;
		}
	}
	.default-btn {
		margin-top: 30px;
	}
	.signature {
		margin-top: 25px;
	}
}
.about-content.left-content {
	max-width: 625px;
	margin-left: auto;
	padding-left: 0;
	padding-right: 15px;
}
.about-right-image {
	position: relative;
	text-align: right;
	padding-left: 100px;
	padding-bottom: 60px;
	img {
		&:nth-child(2) {
			position: absolute;
			left: 0;
			bottom: 0;
		}
	}
	.text-box {
		position: absolute;
		left: 220px;
		top: 60px;
		z-index: 1;
		width: 150px;
		height: 150px;
		border-radius: 50%;
		background-color: $background-color_2;
		-webkit-box-shadow: 6px 5px 25px rgba(0, 0, 0, 0.08);
		box-shadow: 6px 5px 25px rgba(0, 0, 0, 0.08);
		text-align: center;
		.inner {
			position: absolute;
			left: 0;
			top: 55%;
			right: 0;
			-webkit-transform: translateY(-55%);
			transform: translateY(-55%);
			text-transform: uppercase;
			color: $color_3;
			font-size: 15px;
			font-weight: 600;
			span {
				display: block;
				color: $color_1;
				margin-top: -2px;
				font-size: 30px;
				font-weight: 700;
			}
		}
		&::before {
			position: absolute;
			content: "";
			border: 2px dashed #d42323;
			left: 0;
			top: 0;
			border-radius: 50%;
			z-index: -1;
			right: 0;
			bottom: 0;
			margin: 10px;
			-webkit-animation-name: rotateMe;
			animation-name: rotateMe;
			-webkit-animation-duration: 20s;
			animation-duration: 20s;
			-webkit-animation-iteration-count: infinite;
			animation-iteration-count: infinite;
			-webkit-animation-timing-function: linear;
			animation-timing-function: linear;
		}
	}
}
.about-inner-area {
	margin-top: 80px;
}
.about-text {
	h3 {
		position: relative;
		margin-bottom: 20px;
		padding-bottom: 10px;
		border-bottom: 1px solid #eeeeee;
		font-size: 20px;
		font-weight: 700;
		&::before {
			content: "";
			position: absolute;
			left: 0;
			bottom: -1px;
			width: 50px;
			background-color: $background-color_3;
			height: 1px;
		}
	}
	.features-list {
		padding-left: 0;
		list-style-type: none;
		margin-top: 15px;
		margin-bottom: 0;
		li {
			position: relative;
			margin-bottom: 12px;
			color: $color_4;
			padding-top: 3px;
			padding-left: 30px;
			i {
				font-size: 16px;
				color: $color_4;
				background-color: $background-color_4;
				width: 20px;
				height: 20px;
				line-height: 20px;
				border-radius: 50%;
				-webkit-transition: 0.5s;
				transition: 0.5s;
				text-align: center;
				position: absolute;
				left: 0;
				top: 50%;
				-webkit-transform: translateY(-50%);
				transform: translateY(-50%);
			}
			&:last-child {
				margin-bottom: 0;
			}
			&:hover {
				i {
					background-color: $background-color_3;
					color: $color_2;
				}
			}
		}
	}
}